<template>
  <div id="calculator" class="container">
    <div class="calculator-box">
      <div class="person-price" v-show="!isShow">
        <h3>个人费用</h3>
        <ul class="row">
          <li v-for="(people,index) in peoples" :key="index" v-display:display="people" class="col-xs-6 col-md-6">
            <div class="row">
              <div class="col-xs-6 col-md-6 person-item">
                <label>姓名</label>
                <input type="text" v-model="people.name" class="form-control"></div>
              <div class="col-xs-6 col-md-6 person-item">
                <label>价格</label>
                <input type="text" v-model="people.price" v-number class="form-control">
              </div>
            </div>
            <button @click="deletePeople(index)" class="btn btn-default delete-person">删除</button>
          </li>
        </ul>
      </div>

      <button @click="addPeople()" class="add-people" v-show="!isShow">添加鑫の的饭友</button>

      <div class="person-price" v-show="!isShow">
        <h3>公摊费用</h3>
        <ul>
          <li>
            <div class="row">
              <div class="col-xs-6 col-md-6 person-item">
                <label>运费</label>
                <input type="text" v-model="monies.freight" v-number class="form-control">
              </div>
              <div class="col-xs-6 col-md-6 person-item">
                <label>餐盒</label>
                <input type="text" v-model="monies.lunchBoxFee" v-number class="form-control">
              </div>
            </div>
          </li>
        </ul>
      </div>
      
      <div class="person-price" v-show="!isShow">
        <h3>优惠费用</h3>
        <ul>
          <li>
            <div class="row">
              <div class="col-xs-6 col-md-6 person-item">
                <label>满减</label>
                <input type="text" v-model="monies.coupon" v-number class="form-control">
              </div>
              <div class="col-xs-6 col-md-6 person-item">
                <label>红包</label>
                <input type="text" v-model="monies.redEnvelope" v-number class="form-control">
              </div>
            </div>
          </li>
        </ul>
      </div>

      <button @click="settlement()" class="add-people" v-show="!isShow">计算♂的价格</button>

      <div class="person-price" v-show="isShow">
        <h3>账单明细</h3>
        <ul>
          <li>
            <label>运费：{{ monies.freight }}</label>
            <label>餐盒：{{ monies.lunchBoxFee }}</label>
            <label>满减：{{ monies.coupon }}</label>
            <label>红包：{{ monies.redEnvelope }}</label>
            <label>总价：{{ list.totalPrice }}</label>
            <label>人数：{{ list.numberOfPeople }}</label>
          </li>
        </ul>
      </div>

      <div class="person-price" v-show="isShow">
        <ul>
          <li v-for="(count,index) in counts" :key="index">
            <p>姓名：{{ count.name }}</p>
            <p>单价：{{ count.price }}</p>
            <p>公摊费用：{{ count.publickPrice }}</p>
            <p>优惠金额：{{ count.preferentialAmount }}</p>
            <p>需要支付：{{ count.price }} + {{ count.publickPrice }} - {{ count.preferentialAmount }} = {{ count.paymentPrice }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "calculator",
  data() {
    return {
      msg: "鑫の外卖计算器",
      peoples: [
        { name: "逼格鑫", price: 0 },
        { name: "鑫の饭友1", price: 0 }
      ],
      monies: {
        freight: 0,
        lunchBoxFee: 0,
        coupon: 0,
        redEnvelope: 0
      },
      counts: [],
      list: {
        totalPrice: 0,
        numberOfPeople: 0
      },
      isShow: false
    };
  },
  methods: {
    deletePeople: function(index) {
      this.peoples.splice(index, 1);
    },
    addPeople: function() {
      this.peoples.push({
        name: "",
        price: 0
      });
    },
    settlement: function() {
      var numberOfPeople = this.peoples.length;
      var a = parseFloat(number_format(this.monies.freight / numberOfPeople,2,".",",","floor"));
      var b = parseFloat(number_format(this.monies.lunchBoxFee / numberOfPeople,2,".",",","floor"));
      var c = parseFloat(number_format(this.monies.coupon / numberOfPeople, 2, ".", ",", "floor"));
      var d = parseFloat(number_format(this.monies.redEnvelope / numberOfPeople,2,".",",","floor"));
      var f = parseFloat(number_format(a + b - c - d, 2, ".", ",", "floor"));
      var g = parseFloat(number_format(a + b, 2, ".", ",", "floor"));
      var j = parseFloat(number_format(c + d, 2, ".", ",", "floor"));
      // console.log(numberOfPeople,a,b,c,d,f);
      console.log(this.list.totalPrice);
      for (var i = this.peoples.length - 1; i >= 0; i--) {
        var h = parseFloat(this.peoples[i].price) + f;
        this.list.totalPrice =
          this.list.totalPrice + parseFloat(this.peoples[i].price);
        this.counts.push({
          name: this.peoples[i].name,
          price: parseFloat(this.peoples[i].price),
          preferentialAmount: j,
          publickPrice: g,
          paymentPrice: h
        });
      }
      console.log(this.list.totalPrice);
      this.list.numberOfPeople = numberOfPeople;
      this.isShow = true;
      //console.log(this.count)
    }
  },
  directives: {
    display: {
      bind(el, binding, vnode) {
        if (binding.value.name == "" || binding.value.price == 0) {
          el.style.background =
            "#" +
            Math.random()
              .toString(16)
              .slice(2, 8);
        }
      },
      update(el, binding) {
        if (binding.value.name != "" && binding.value.price != 0) {
          el.style.background = "#fff";
        } else if (binding.value.name == "" || binding.value.price == 0) {
          el.style.background =
            "#" +
            Math.random()
              .toString(16)
              .slice(2, 8);
        }
      }
    },
    number: {
      bind(el, binding, vnode) {
        el.handler = function() {
          el.value = el.value.replace(/[^\d]/g, "");
        };
        el.addEventListener("input", el.handler);
      }
    }
  }
};
function number_format(number, decimals, dec_point, thousands_sep, roundtag) {
  /*
    * 参数说明：
    * number：要格式化的数字
    * decimals：保留几位小数
    * dec_point：小数点符号
    * thousands_sep：千分位符号
    * roundtag:舍入参数，默认 "ceil" 向上取,"floor"向下取,"round" 四舍五入
    * */
  number = (number + "").replace(/[^0-9+-Ee.]/g, "");
  roundtag = roundtag || "ceil"; //"ceil","floor","round"
  var n = !isFinite(+number) ? 0 : +number,
    prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
    sep = typeof thousands_sep === "undefined" ? "," : thousands_sep,
    dec = typeof dec_point === "undefined" ? "." : dec_point,
    s = "",
    toFixedFix = function(n, prec) {
      var k = Math.pow(10, prec);
      console.log();

      return (
        "" +
        parseFloat(
          Math[roundtag](parseFloat((n * k).toFixed(prec * 2))).toFixed(
            prec * 2
          )
        ) /
          k
      );
    };
  s = (prec ? toFixedFix(n, prec) : "" + Math.round(n)).split(".");
  var re = /(-?\d+)(\d{3})/;
  while (re.test(s[0])) {
    s[0] = s[0].replace(re, "$1" + sep + "$2");
  }

  if ((s[1] || "").length < prec) {
    s[1] = s[1] || "";
    s[1] += new Array(prec - s[1].length + 1).join("0");
  }
  return s.join(dec);
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
#calculator {
  max-width: 600px;
  margin: 0 auto;
}

#calculator h1 {
  text-align: center;
}
.calculator-box {
  padding: 10px;
  box-sizing: border-box;
  background: #3d9854;
  border: 1px dotted #aaa;
}
.person-price {
  box-sizing: border-box;
  background: #fff;
}
.person-price h3 {
  padding: 10px;
}
.person-price ul {
  border-bottom: 1px solid #000;
}
.person-price ul li {
  padding: 10px;
  border-top: 1px solid #000;
}
.person-price .person-item {
  margin: 5px 0;
}
.person-price label {
  display: inline-block;
  font-size: 16px;
  padding: 5px;
  color: #000;
}
.person-price p {
  font-size: 16px;
  padding: 5px;
}
.person-price input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
.person-price input[type="text"].form-control {
  display: inline-block;
  outline: none;
}
.person-price button {
  padding: 7px;
}
.add-people {
  display: block;
  margin: 20px auto;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
  outline: none;
}
.delete-person {
  display: block;
  margin: 10px auto 0;
}
</style>



